import { NavLink } from 'react-router-dom'
import classNames from 'classnames'
import headerStyles from './header.module.css'

export const Header = () => {

	return (
		    <header className={headerStyles.wr}>
			<nav>
				<ul className={headerStyles.headerMenu}>
					<div>
					<i className="fa-solid fa-dog"></i>
					</div>
					<li>
						<NavLink className={({isActive}) => classNames({[headerStyles.activeLink]: isActive})} to="/">Good Food</NavLink>
					</li>
					<input className={headerStyles.input} type="text" placeholder='...'></input>
					<li>
						<NavLink className={({isActive}) => classNames({[headerStyles.activeLink]: isActive})} to="products/"></NavLink>
					</li>
					<li>
						<NavLink className={({isActive}) => classNames({[headerStyles.activeLink]: isActive})} to="signin/"></NavLink>
					</li>
					<li>
						<NavLink className={({isActive}) => classNames({[headerStyles.activeLink]: isActive})} to="signup/"></NavLink>
					</li>
				</ul>
			</nav>
    </header>
	)
}